import React, { useCallback, useState } from 'react';
import {
  Button,
  Card,
  Col,
  Container,
  Dropdown,
  DropdownButton,
  Form,
  Nav,
  Navbar,
  Row,
  Stack,
  ToggleButton,
  ToggleButtonGroup
} from 'react-bootstrap'
import { FaCopy, FaFile, FaPlay, FaSave, } from 'react-icons/fa';
import { Base64 } from 'js-base64';
import { useDropzone } from 'react-dropzone';
import FileSaver from 'file-saver';

import 'bootstrap/dist/css/bootstrap.min.css';
import '../styles/common.css'

const ENCODINGS = [
  { code: 'utf-8', name: 'ASCII/UTF-8' },
  { code: 'utf-16le', name: 'UTF-16' },
  { code: 'utf-16le', name: 'UTF-16LE' },
  { code: 'utf-16be', name: 'UTF-16BE' },
  { code: 'windows-1252', name: 'ASCII' },
  { code: 'windows-1252', name: 'Windows-1252' },
  { code: 'big5', name: 'Big5 (Chinese)' },
  { code: 'ibm866', name: 'CP866 (Russian)' },
  { code: 'euc-jp', name: 'EUC-JP (Japanese)' },
  { code: 'euc-kr', name: 'EUC-KR (Korean)' },
  { code: 'gb18030', name: 'GB 18030 (Chinese)' },
  { code: 'hz-gb-2312', name: 'GB 2312 (Chinese)' },
  { code: 'replacement', name: 'ISO-2022-CN (Chinese)' },
  { code: 'iso-2022-jp', name: 'ISO-2022-JP (Japanese)' },
  { code: 'windows-1252', name: 'ISO-8859-1 (Latin1/Western European)' },
  { code: 'iso-8859-2', name: 'ISO-8859-2 (Latin2/Eastern European)' },
  { code: 'iso-8859-3', name: 'ISO-8859-3 (Latin3/South European)' },
  { code: 'iso-8859-4', name: 'ISO-8859-4 (Latin4/North European)' },
  { code: 'iso-8859-5', name: 'ISO-8859-5 (Latin/Cyrillic)' },
  { code: 'iso-8859-6', name: 'ISO-8859-6 (Latin/Arabic)' },
  { code: 'iso-8859-7', name: 'ISO-8859-7 (Latin/Greek)' },
  { code: 'iso-8859-8', name: 'ISO-8859-8 (Latin/Hebrew)' },
  { code: 'iso-8859-8-i', name: 'ISO-8859-8-I (Latin/Hebrew)' },
  { code: 'iso-8859-10', name: 'ISO-8859-10 (Latin6/Nordic)' },
  { code: 'iso-8859-13', name: 'ISO-8859-13 (Latin7/Baltic Rim)' },
  { code: 'iso-8859-14', name: 'ISO-8859-14 (Latin8/Celtic)' },
  { code: 'iso-8859-15', name: 'ISO-8859-15 (Latin9/Western European)' },
  { code: 'iso-8859-16', name: 'ISO-8859-16 (Latin10/South-Eastern European)' },
  { code: 'koi8-r', name: 'KOI8-R (Russian)' },
  { code: 'koi8-u', name: 'KOI8-U (Ukrainian)' },
  { code: 'macintosh', name: 'Macintosh (x-mac-roman)' },
  { code: 'x-mac-cyrillic', name: 'Mac OS Cyrillic (x-mac-cyrillic)' },
  { code: 'shift_jis', name: 'Shift JIS (Japanese)' },
  { code: 'windows-874', name: 'Windows-874 (Thai)' },
  { code: 'windows-1250', name: 'Windows-1250 (Eastern European)' },
  { code: 'windows-1251', name: 'Windows-1251 (Cyrillic)' },
  { code: 'windows-1252', name: 'Windows-1252 (Western European)' },
  { code: 'windows-1253', name: 'Windows-1253 (Greek)' },
  { code: 'windows-1254', name: 'Windows-1254 (Turkish)' },
  { code: 'windows-1255', name: 'Windows-1255 (Hebrew)' },
  { code: 'windows-1256', name: 'Windows-1256 (Arabic)' },
  { code: 'windows-1257', name: 'Windows-1257 (Baltic)' },
  { code: 'windows-1258', name: 'Windows-1258 (Vietnamese)' },
  { code: 'x-user-defined', name: 'X-User-Defined' },
];

export default () => {
  const [input, setInput] = useState<string>('');
  const [isValid, setValid] = useState(true);
  const [output, setOutput] = useState<string>();

  const handleTextEncodingClick = (selection: any) => {
    const encoding = JSON.parse(selection);
    if (encoding) {
      setTextEncoding(encoding);
    }
  }
  const [textEncoding, setTextEncoding] = useState(ENCODINGS[0]);

  const OUTPUT_TYPES = [
    {
      id: 'text',
      label: 'Text',
      jsx: (textEncoding: typeof ENCODINGS[0]) => (
        <Stack direction="horizontal" gap={3}>
          <span style={{ display: 'inline-block' }}>Text encoding to use:</span>
          <DropdownButton id="dropdown-basic-button" variant="outline-dark" title={textEncoding.name} onSelect={handleTextEncodingClick} >
            {
              ENCODINGS.map(encoding => <Dropdown.Item key={encoding.code + '-' + encoding.name} eventKey={JSON.stringify(encoding)} >{encoding.name}</Dropdown.Item>)
            }
          </DropdownButton>
        </Stack >
      )
    },
    {
      id: 'image',
      label: 'Image',
      jsx: () => (
        <>
          <span style={{ paddingRight: '10px' }}>Input image format:</span>
        </>
      )
    },
    {
      id: 'hex',
      label: 'Hexadecimal',
      jsx: () => (
        <>
          <span style={{ paddingRight: '10px' }}>Output formatting:</span>
        </>
      )
    },
    {
      id: 'bin',
      label: 'Binary',
      jsx: () => (
        <>
          <span style={{ paddingRight: '10px' }}>Output formatting:</span>
        </>
      )
    }
  ];
  const [outputType, setOutputType] = useState(OUTPUT_TYPES[0]);

  const handleFileDrop = useCallback(acceptedFiles => {
    if (acceptedFiles && acceptedFiles[0]) {
      loadFile(acceptedFiles[0]);
    }
  }, [])
  const { getRootProps, isDragActive } = useDropzone({
    onDrop: handleFileDrop,
    noClick: true
  });

  const loadFile = (file: File) => {
    const reader = new FileReader();
    reader.addEventListener('load', (event) => {
      const content = event.target?.result;
      if (typeof content == 'string') {
        handleInput(content);
      }
    });
    reader.readAsText(file);
  }

  const handleLoadFile = () => {
    let input = document.createElement('input');
    input.type = 'file';
    input.onchange = () => {
      console.log(input.files);
      if (input.files && input.files[0]) {
        loadFile(input.files[0]);
      }
    };
    input.click();
  }

  const handleInput = (input: string) => {
    try {
      let bytes = Base64.toUint8Array(input);
      let text = new TextDecoder(textEncoding.code).decode(bytes);
      setValid(true);
    } catch {
      setValid(false);
    }

    setOutput(undefined);
    setInput(input);
  };

  const handleDecode = () => {
    if (isValid) {
      let bytes = Base64.toUint8Array(input);
      let text = new TextDecoder(textEncoding.code).decode(bytes);
      setOutput(text);
    } else {
      console.log('Invalid input. This should not happen.')
    }
  };

  const handleCopy = () => {
    if (output) {
      navigator.clipboard.writeText(output);
    } else {
      console.log('Invalid output. This should not happen.')
    }
  };

  const handleSave = () => {
    if (output) {
      let blob = new Blob([output], { type: "text/plain;charset=utf-8" });
      FileSaver.saveAs(blob, "output.txt");
    } else {
      console.log('Invalid output. This should not happen.')
    }
  };

  return (
    <Stack>
      <Navbar bg="light" expand="lg" style={{ marginBottom: '20px' }}>
        <Container>
          <Navbar.Brand href="#home"><strong>Base64</strong>Tools.org</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto" >
              <Nav.Link href="#what-is-base64">What is Base64?</Nav.Link>
              <Nav.Link href="https://datatracker.ietf.org/doc/html/rfc4648">Base64 Specification</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar >

      <Container>
        <Row>
          <p>
            Base64 decoding and encoding tools with support for text, images and files - all with your data never leaving the browser.
          </p>
        </Row>

        <Row>
          <Col lg={9} sm={12}>
            <p>
              <Card>
                <Card.Header>
                  <Nav variant="tabs" defaultActiveKey="#decoding">
                    <Nav.Link href="#decoding">Decoding</Nav.Link>
                    <Nav.Link href="#encoding">Encoding</Nav.Link>
                  </Nav>
                </Card.Header>
                <Card.Body>
                  <Container>
                    <Row>
                      <p>
                        <Button variant="outline-dark" onClick={handleLoadFile}> <FaFile />  Load file </Button> or, paste the base64 encoded data, or drop the file in the text box below.
                      </p>
                    </Row>
                    <Row>
                      <p {...getRootProps()}>
                        <Form.Control as="textarea" isInvalid={!isValid} style={isDragActive ? { outline: '2px dotted' } : {}} value={input} onChange={e => handleInput(e.target.value)} rows={5} placeholder="base64 input goes here." />
                      </p>
                    </Row>
                    <Row>
                      <p>
                        <span style={{ paddingRight: '10px' }}>Output type:</span>
                        <ToggleButtonGroup type='radio' name='outputType' value={outputType.id}>
                          {OUTPUT_TYPES.map(type => (
                            <ToggleButton variant="outline-dark" value={type.id} onClick={() => setOutputType(type)}>{type.label}</ToggleButton>
                          ))}
                        </ToggleButtonGroup>
                      </p>
                    </Row>
                    <Row>
                      <p>
                        {outputType.jsx(textEncoding)}
                      </p>
                    </Row>
                    <Row>
                      <Container>
                        <Button variant="primary" disabled={!isValid} onClick={handleDecode}> <FaPlay />  Decode </Button>
                      </Container>
                    </Row>
                  </Container>
                </Card.Body>
              </Card>
            </p>
            <p>
              {output !== undefined ?
                <Card>
                  <Card.Header>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                      Decoded output
                      <Stack float-right direction='horizontal' gap={3} >
                        <Button variant="primary" onClick={handleCopy}> <FaCopy />  Copy to clipboard </Button>
                        <Button variant="primary" onClick={handleSave}> <FaSave />  Save as file </Button>
                      </Stack>
                    </div>
                  </Card.Header>
                  <Card.Body>
                    <Container>
                      <Card.Text>
                        {output}
                      </Card.Text>
                    </Container>
                  </Card.Body>
                </Card>
                :
                null
              }
            </p>
          </Col>
          <Col lg={3} sm={12}>
            <Stack>
              {/* <div style={{ height: 300, backgroundColor: 'grey', marginBottom: '20px' }}></div> */}
              <Card>
                <Card.Header>Privacy and confidentiality</Card.Header>
                <Card.Body>
                  <Card.Text>
                    This tool processes the data you supply completely within your browser and is NOT transmissed out to the internet.
                  </Card.Text>
                </Card.Body>
              </Card>
            </Stack>
          </Col>
        </Row>

        <Row>
          <Navbar bg="light" expand="lg" style={{ marginTop: '20px' }}>
            <Container>
              <Nav className="ms-auto me-auto" >
                <Nav.Link href="/">© base64tools.org</Nav.Link>
                <Nav.Link href="/about">About</Nav.Link>
                <Nav.Link href="/about#terms">Terms of Use</Nav.Link>
                <Nav.Link href="/about#privacy">Privacy Policy</Nav.Link>
                <Nav.Link href="/cookies">Manage Cookies</Nav.Link>
                <Nav.Link href="/report">Report an issue</Nav.Link>
              </Nav>
            </Container>
          </Navbar>
        </Row>
      </Container >

    </Stack >
  )
};
